import React, { useState } from "react";
import Women from "../pictures/Women.png";
import Modal from "./Modal";
function Starter() {
  const [status, setStatus] = useState(false);

  return (
    <>
      {status && (
        <Modal closeModal={() => setStatus(false)}>
          <span>The content of the modal</span>
          <span>The content of the modal</span>
          <span>The content of the modal</span>

          <span>The content of the modal</span>

          <span>The content of the modal</span>
        </Modal>
      )}
      <div className="top-menu">
        <img
          style={{ height: "60px", width: "200px" }}
          src={Women}
          alt="Women's beauty"
        />
        <span onClick={() => setStatus(true)}>MAKE UP</span>
        <span>SKIN CARE</span>
        <span>HAIR</span>
        <span>MEN</span>
        <span>VIRTUAL TRY ON</span>
        <span>BEAUTY MAGAZINE</span>
      </div>
    </>
  );
}

export default Starter;
